@import "setup";
@import "variables";
@import "typography";

.wrapper {
	max-width: 1280px;
	width: 90%;
	margin: 0 auto;
}

section {
	padding-bottom: 8%;
}

.container,
ul {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

ul {
	list-style-type: none;
	padding: 0;
}

a {
	text-decoration: none;
}

a:hover,
button:hover {
	cursor: pointer;
}

