header {
	padding-top: 5%;

	h1 {
		margin-bottom: 0;
		padding-top: 5%;
	}

	nav {
		float: right;
		width: min(35%, 35rem);
		position: relative;

		a:hover::after {
			content: "";
			width: 21%;
			height: 0.25rem;
			background: $primaryFontColor;
			position: absolute;
			bottom: 20%;
		}
	}

	.mainHeading {
		padding-bottom: 5%;
	}

	.personalInfo {
		padding-bottom: 0;
	}

	.socialMedia ul {
		width: max(12%, 10rem);

		a {
			width: max(3%, 3rem);
		}

		a:hover, a:focus {
			outline: 2px solid $primaryFontColor;
			border-radius: 2.5%;
		}

		img {
			width: max(3%, 30px);
		}
	}
}
.about {
	display: flex;
	justify-content: space-evenly;
}

.coreCompetencies {
	li {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		flex-wrap: wrap;
		width: max(10%, 10rem);
	}

	img {
		display: flex;
		width: 30px;
	}
}

.card {
	display: flex;
	flex-direction: column;
	width: max(30%, 20rem);

}

.projects {
	.container {
		margin-bottom: 1%;
	}

	p {
		margin-bottom: 7.5%;
	}
}

.codeLinks {
	display: flex;
	justify-content: space-around;
	position: relative;

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 35%;
	}

	a:hover, a:focus {
		outline: 2px solid $primaryFontColor;
		border-radius: 2.5%;
	}

	img {
		margin-right: 2%;
		width: 2.5rem;
		height: 2.5rem;
	}
}

.portfolioLink {
	display: flex;
	position: relative;
	width: 17%;

	a:hover::after {
		content: "";
		width: 95%;
		height: 0.25rem;
		background: $primaryFontColor;
		position: absolute;
		bottom: 20%;
	}
}


.workExperienceContainer {
	display: flex;
	flex-wrap: wrap;

	.card {
		margin-right: 10%;
	}
}

.contact {
	display: flex;
	justify-content: center;
	flex-direction: column;

	form {
		display: flex;
		flex-direction: column;

		label,
		input,
		textarea {
			margin-bottom: 1%;
		}

		input {
			width: min(50%, 25rem);
		}

		textarea {
			width: min(70%, 45rem);
			resize: none;
		}

		button {
			width: min(20%, 8rem);
		}

		button:hover,
		button:focus {
			background-color: $primaryFontColor;
			color: white;
			cursor: pointer;
		}
	}
}

.returnToTop {
	display: flex;
	justify-content: center;

	a {
		padding: 1%;
		border: 2px solid $primaryFontColor;
	}

	a:hover,
	a:focus {
		background-color: $primaryFontColor;
		color: white;
	}
}

footer {
	display: flex;
	justify-content: center;
}

.homeAnchor:hover::after {
		content: "";
		width: 15%;
		height: 0.25rem;
		background: $primaryFontColor;
		position: absolute;
		bottom: 20%;
	
}

.portfolioHeading {
	padding-bottom: 10%;
}

.portfolioProjects {
	.card {
		margin-bottom: 5%;
	}
}

@media screen and(max-width: 450px) {
	.card {
		padding-bottom: 10%;
	}
}
